
.dialog-custom {
  overflow: hidden;
}
.dialog-custom .dialog-tit {
  width: 80%;
  font-size: 14px;
}
.dialog-custom .el-dialog__body {
  padding-bottom: 0;
}
.dialog-custom .dialog-full {
  position: absolute;
  right: 45px;
  top: 0px;
  height: 42px;
  width: 20px;
  cursor: pointer;
  text-align: center;
}
.dialog-custom .dialog-full img {
  display: block;
  margin-top: 14px;
  width: 14px;
  height: 14px;
}
.dialog-custom .el-dialog {
  position: absolute;
  width: 530px;
  left: calc(50% - 265px);
  top: 15%;
}
.dialog-custom .el-dialog .dialog-custom-con {
  position: relative;
  overflow-y: auto;
  max-height: 415px;
  z-index: 3;
}
.dialog-custom .el-dialog .el-button,
.dialog-custom .el-dialog .el-checkbox,
.dialog-custom .el-dialog .el-tabs__item {
  font-weight: normal;
}
.dialog-custom .el-dialog .dialog-custom-hidden {
  overflow: hidden;
}
.dialog-custom .el-dialog .el-dialog__header {
  padding: 0 20px;
  line-height: 42px;
  height: 42px;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  color: #4a4a4a;
  background: #f9f9f9;
  -webkit-box-shadow: 0px 1px 0px 0px #dddddd;
          box-shadow: 0px 1px 0px 0px #dddddd;
}
.dialog-custom .el-dialog .el-dialog__header .el-dialog__headerbtn {
  top: 0;
  line-height: 42px;
}
.dialog-custom .el-dialog .el-dialog__footer {
  text-align: center;
  padding: 10px 20px;
}
.dialog-custom .el-dialog .el-dialog__footer .el-button {
  width: 90px;
  height: 30px;
  font-size: 12px;
  padding: 0 15px;
}
.dialog-custom .el-dialog .el-dialog__footer .el-button + .el-button {
  margin-left: 30px;
}
.dialog-custom .el-dialog .el-form {
  margin: 0 auto;
}
.dialog-custom .el-dialog .el-form .el-form-item__content,
.dialog-custom .el-dialog .el-form .el-form-item__label {
  line-height: 38px;
  font-size: 12px;
}
.dialog-custom .el-dialog .el-form .el-form-item__label {
  color: #4a4a4a;
}
.dialog-custom .el-dialog .el-form .el-form-item {
  margin: 10px auto 0 auto !important;
}
.dialog-custom .el-dialog .el-form .el-form-item input {
  height: 38px;
  line-height: 38px;
}
.dialog-custom .el-dialog .el-form .el-input {
  width: 100%;
}
.dialog-custom .el-dialog .el-form .el-form-item__error {
  width: 100%;
  position: relative;
  padding-top: 5px;
}
.dialog-custom .el-dialog .el-form {
  overflow: hidden;
}
.dialog-rb {
  position: absolute;
  right: 0;
  height: 15px;
  width: 15px;
  bottom: 0;
  z-index: 8;
  cursor: se-resize;
}
.dialog-move-bg {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: transparent;
  z-index: 99999;
  display: none;
  cursor: move;
}
.diogFull {
  width: 100%;
}
.diogFull .el-dialog {
  width: 100% !important;
  left: 0 !important;
  top: 0;
  bottom: 0;
  margin: 0;
}
.diogFull .el-dialog .dialog-custom-con {
  max-height: 100% !important;
  height: 100% !important;
}
.diogFull .el-dialog .el-dialog__body {
  max-height: calc(100% - 102px) !important;
  height: calc(100% - 102px) !important;
}



